body {
  display: block;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  /* Установка цвета фона на указанный оттенок */
  background-color: #ffa6da;
}
.breed-select {
  /* Увеличение ширины элемента для помещения полного названия породы */
 min-width: 255px;
  /* Уменьшение размера текста внутри элемента */
  font-size: 20px;
  width: 150px;
  display: none;
}
.error {
  display: none;
}
.cat-info {
  display: flex;
  flex-direction: row;
  gap: 20px;
}
.loader {
  position: absolute;
  left: 50vmin;
  top: 300px;
}
.loader {
  /* Стили для центрирования элемента */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /* Остальные стили для анимированного котика:) */
  perspective: 1000px;
  border-radius: 50%;
  width: 400px;
  height: 300px;
  color: #53ff5c;

}
    .loader:before,
    .loader:after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: inherit;
      height: inherit;
      border-radius: 50%;
      transform: rotateX(70deg);
      animation: 1s spin linear infinite;
    }
    .loader:after {
      color: #FF3D00;
      transform: rotateY(70deg);
      animation-delay: .4s;
    }

  @keyframes rotate {
    0% {
      transform: translate(-50%, -50%) rotateZ(0deg);
    }
    100% {
      transform: translate(-50%, -50%) rotateZ(360deg);
    }
  }

  @keyframes rotateccw {
    0% {
      transform: translate(-50%, -50%) rotate(0deg);
    }
    100% {
      transform: translate(-50%, -50%) rotate(-360deg);
    }
  }

  @keyframes spin {
    0%,
    100% {
      box-shadow: .2em 0px 0 0px currentcolor;
    }
    12% {
      box-shadow: .2em .2em 0 0 currentcolor;
    }
    25% {
      box-shadow: 0 .2em 0 0px currentcolor;
    }
    37% {
      box-shadow: -.2em .2em 0 0 currentcolor;
    }
    50% {
      box-shadow: -.2em 0 0 0 currentcolor;
    }
    62% {
      box-shadow: -.2em -.2em 0 0 currentcolor;
    }
    75% {
      box-shadow: 0px -.2em 0 0 currentcolor;
    }
    87% {
      box-shadow: .2em -.2em 0 0 currentcolor;
    }
  }
